<template>
  <div ref="remote"></div>
</template>

<script>
export default {
  props: {
    src: {
      type: String,
      default: ''
    }
  },
  mounted() {
    const s = document.createElement('script')
    s.type = 'text/javascript'
    s.src = this.src
    this.$refs.remote.appendChild(s)
  }
}
</script>



